.perf-no-animation * {
    transition: none !important;
    -webkit-animation: none !important;
    animation: none !important;
}

html {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    background: $white;
    color: $black;
    letter-spacing: normal;
    font-family: 'Product Sans', sans-serif;
}

button:active,
button:hover,
input:active,
input:focus,
input:hover {
    outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
    line-height: 130%;
}

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
    color: $black;
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
}

ul {
    list-style-type: none;
}

section {
    overflow: hidden;
    position: relative;
}

body {
    &.page {
        display: flex;
        flex-direction: column;
        min-height: 100vh;
        min-height: calc(var(--vh, 1vh) * 100);
        background: $white;
    }

    &.mobile::after {
        content: "";
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        z-index: 999;
        -webkit-animation: 0.3s opacity;
        animation: 0.3s opacity;
    }

    @-webkit-keyframes opacity {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }

    @keyframes opacity {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }
}

main {
    width: 100%;
    padding-top: 69px;
}

.container {
    margin: 0 auto;
    max-width: 1540px;
    padding: 0 50px;

    @media screen and (max-width: $w1440) {
        max-width: 100%;
    }

    @media screen and (max-width: $w1024) {
        padding: 0 40px;
    }

    @media screen and (max-width: $w480) {
        padding: 0 24px;
    }
}

.edit-content * {
    font-family: 'Product Sans', sans-serif !important;
}

.edit-content p {
    font-size: 14px;
    line-height: 160%;
    font-weight: normal !important;
    color: $textPrimary;
}

.edit-content p img {
    margin: 0 auto;
}

.edit-content ol {
    list-style: none;
    padding: 0;
    counter-reset: item
}

.edit-content ol li {
    padding-left: 23px;
    position: relative;
    counter-increment: item;
    font-size: 15px;
    line-height: 180%;
}

.edit-content ol li::before {
    content: counter(item, decimal-leading-zero) ".";
    position: absolute;
    top: .15em;
    left: 0;
    display: block;
    font-weight: 700;
    font-size: 14px;
    line-height: 180%;
}

.edit-content ul {
    padding: 17px 0
}

.edit-content ul li {
    padding-left: 23px;
    position: relative;
    font-size: 15px;
    line-height: 180%;
}

.edit-content ul li::before {
    content: "";
    position: absolute;
    top: .7em;
    left: 0;
    display: block;
    width: 7px;
    height: 7px;
    background: $black;
    border-radius: 50%
}

.edit-content table {
    width: 100% !important;
}

.edit-content table img {
    width: 100% !important;
    height: auto !important;
}